export interface SeriaGUS {
    id: number;
    level?: string;
    n1?: string;
    n2?: string;
    n3?: string;
    n4?: string;
    n5?: string;
    measureUnitName?: string;
    years?: number[];
}

export interface Wykres {
    typ_wykresu: "Kolumnowy (XY)" | "Liniowy (XY)" | "GUS: Kolumnowy" | "GUS: Liniowy" | "Kołowy" | "Pierścieniowy"
    opis: string;
    jednostka?: string;
    procent_calosci?: number;
    [k: number]: SeriaGUS;
    gus_lata?: number;
    dane_xy?: WykresXYDane[];
    dane_kolowy?: NameValue[];
    kolory?: "paleta" | "odcienie" | "rozbiezne"
    per_capita?: boolean;
    ludnosc?: number;
}

