import { combineReducers } from "@reduxjs/toolkit";
import {
    autor,
    cytat,
    logo,
    lista_powiatow,
    kod_gminy,
    wojewodztwo,
    dzial,
    moduly,
    tytul
} from "./konfiguracja/reducers";
import {dane_gminy, dane_podregionu} from "./dane/reducers";
import {grupa_dzial, grupa_rozdzial, wykresy} from "./infografika/reducers";


const rootReducer = combineReducers({
    autor,
    cytat,
    kod_gminy,
    wojewodztwo,
    dane_gminy,
    logo,
    dane_podregionu,
    lista_powiatow,
    grupa_dzial,
    grupa_rozdzial,
    wykresy,
    moduly,
    dzial,
    tytul,
});

export default rootReducer;

export type RootState = ReturnType<typeof rootReducer>