import {
    ADD_WYKRES,
    CHANGE_GRUPA_DZIAL,
    CHANGE_GRUPA_ROZDZIAL, DELETE_WYKRES, WykresAction,
    MOVE_DOWN_WYKRES, MOVE_UP_WYKRES, SET_WYKRES,
    StringAction,
} from "../action";
import {Wykres} from "./Wykres";

export const grupa_dzial = (state = 'Wynagrodzenia i składki', action: StringAction) => {
    switch (action.type) {
        case CHANGE_GRUPA_DZIAL:
            return action.payload;
        default:
            return state;
    }
};

export const grupa_rozdzial = (state = 'Wynagrodzenia i składki', action: StringAction) => {
    switch (action.type) {
        case CHANGE_GRUPA_ROZDZIAL:
            return action.payload;
        default:
            return state;
    }
};

const initial_wykresy: Wykres[] = [
  {
    typ_wykresu: "GUS: Liniowy",
    opis: "Tereny zieleni|parki:S:A,zieleńce:S:A,zieleń uliczna:S:A,zieleń lokalna:S:A,zieleń osiedlowa:S:A",
    73847: {id: 73847},
    73848: {id: 73848},
    73849: {id: 73849},
    73850: {id: 73850},
    73852: {id: 73852},
    gus_lata: 3
  }
  ];

export const wykresy = (state = initial_wykresy, action: WykresAction) => {
    console.log(action);
    if (["ADD_WYKRES", "DELETE_WYKRES", "SET_WYKRES", "MOVE_DOWN_WYKRES", "MOVE_UP_WYKRES", "SET_SERIE_GUS", "REMOVE_SERIE_GUS"].indexOf(action.type) === -1) {
        return state;
    }
    const { index, wykres } = action.payload;
    switch (action.type) {
        case ADD_WYKRES:
            if (wykres === undefined) {
                console.log("Brak wartosci dla ADD_WYKRES");
                return state;
            }
            return [...state, wykres];
        case DELETE_WYKRES:
            if (index === undefined) {
                console.log("Brak indexu dla DELETE_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index),
                ...state.slice(index + 1)
            ];
        case SET_WYKRES:
            if (index === undefined || wykres === undefined) {
                console.log("Brak indexu lub gus_input dla SET_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index),
                {...(state[index]), ...wykres},
                ...state.slice(index + 1)
            ];
        case MOVE_UP_WYKRES:
            if (index === undefined || index < 1) {
                console.log("Brak lub zly indeks dla MOVE_UP_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index - 1),
                ...state.slice(index, index + 1),
                ...state.slice(index - 1, index),
                ...state.slice(index + 1)
            ]
        case MOVE_DOWN_WYKRES:
            if (index === undefined || index > state.length - 2) {
                console.log("Brak lub zly indeks dla MOVE_DOWN_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index),
                ...state.slice(index + 1, index + 2),
                ...state.slice(index, index + 1),
                ...state.slice(index + 2)
            ]
        default:
            return state;
    }
}
