import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {changeWojewodztwo, pobierzListePowiatow} from "./konfiguracja/actions";
import {RootState} from "./reducers";
import Dropdown from 'react-dropdown';

const WOJEWODZTWA = [
    "DOLNOŚLĄSKIE",
    "KUJAWSKO-POMORSKIE",
    "LUBELSKIE",
    "LUBUSKIE",
    "ŁÓDZKIE",
    "MAŁOPOLSKIE",
    "MAZOWIECKIE",
    "OPOLSKIE",
    "PODKARPACKIE",
    "PODLASKIE",
    "POMORSKIE",
    "ŚLĄSKIE",
    "ŚWIĘTOKRZYSKIE",
    "WARMIŃSKO-MAZURSKIE",
    "WIELKOPOLSKIE",
    "ZACHODNIOPOMORSKIE",
]

class SelektorWojewodztwaInternal extends React.Component<PropsFromRedux> {
    render() {
        const {pobierzListePowiatow, wojewodztwo, changeWojewodztwo} = this.props;
        return <Dropdown
            value={wojewodztwo}
            onChange={ev => {
                changeWojewodztwo(ev.value);
                pobierzListePowiatow(ev.value);
            }}
            options={WOJEWODZTWA}>
        </Dropdown>;
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        wojewodztwo: state.wojewodztwo,
    }
}

const mapDispatchToProps = {
    pobierzListePowiatow,
    changeWojewodztwo,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(SelektorWojewodztwaInternal);
